.modal-text {
  position: relative;
  padding: 16px 20px;
  border-radius: 10px;
  .title {
    font-family: 'PingFang HK', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
  }
  .detail {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: justify;
    margin-top: 10px;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.flexible-text {
  background: #fff7e8;
  .title {
    color: #e0a22e;
  }
  .detail {
    max-width: 210px;
    width: 76%;
