    color: #333333;
    &:last-child {
      margin-right: 0;
      margin-left: 2px;
    }
  }
  .ant-radio-inner {
    width: 12px;
    height: 12px;
    border-color: #5915e1;
    top: -1px;
  }
  .ant-radio-checked .ant-radio-inner {
    border: 1px solid #5915e1;

    &::after {
      width: 6px;
      height: 6px;
    }
  }
}

